import { Machine, assign, spawn } from 'xstate'
import { map } from 'rxjs/operators'

import { resolveDocData } from '../data/utils'

import { petObservableFromRefFactory } from '../data/pet'
import { addEvent } from '../data/timeline'
import { timelineObservableFromRefFactory } from '../data/timeline'

import Event from '@/models/timeline/event'

import { EventTypes } from '@/enums/moment'

// const { humanRefFromIdFactory, humanObservableFromRefFactory, addPet } = require('../data/human')
// const { createPet } = require('../data/pet')

// OBSERVERS
const observePet = assign({
  observer: (context) => {
    const observable = petObservableFromRefFactory(context.pet.ref)
      .pipe(map(pet => ({ type: 'PET.UPDATE', pet: resolveDocData(pet) })))
    return spawn(observable, 'pet-observer')
  }
})

// ACTIONS
const updatePet = assign({
  pet: (context, { pet }) => {
    const timelineObservable = timelineObservableFromRefFactory(pet.timeline)
      .pipe(map(timeline => ({ type: 'TIMELINE.UPDATE', timeline: resolveDocData(timeline) })))
    const timelineObserver = spawn(timelineObservable, 'timeline-observer')
    return {
      ...context.pet,
      ...pet,
      timelineRef: pet.timeline,
      timeline: null,
      timelineObserver
    }
  }
})
const updateTimeline = assign({
  pet: (context, event) => ({
    ...context.pet,
    timeline: event.timeline
  })
})
const createEvent = (context, { type }) => {
  addEvent(context.pet.timelineRef, new Event({ type }))
}

// MACHINE DEFS
const createEventTransitions = () => Object.values(EventTypes).reduce((acc, eventType) => {
  acc[eventType] = { actions: ['createEvent'] }
  return acc
}, {})

export const createPetMachine = ref => Machine({
    initial: 'kennel',
    context: {
      pet: {
        ref
      },
      observer: null
    },
    states: {
      kennel: {
        entry: ['observePet']
      }
    },
    on: {
      ...createEventTransitions(),
      'PET.UPDATE': {
        actions: ['updatePet']
      },
      'TIMELINE.UPDATE': {
        actions: ['updateTimeline']
      }
    }
  },
  {
    actions: {
      observePet,
      updatePet,
      updateTimeline,
      createEvent,
      log: (context) => console.log(context)
    }
  })
