const firebase = require('firebase')

const arrayUnion = firebase.firestore.FieldValue.arrayUnion
const arrayRemove = firebase.firestore.FieldValue.arrayRemove

const resolveDocDataAsync = promise => new Promise((resolve, reject) => {
  promise.then(doc => {
    if (doc.exists) {
      resolve(doc.data())
    } else {
      reject('Doc does not exist')
    }
  }, error => {
    reject(error)
  })
})

const resolveDocData = doc => {
  if (doc.exists) {
    return doc.data()
  } else {
    throw 'Doc does not exist'
  }
}

module.exports = {
  resolveDocDataAsync,
  resolveDocData,
  arrayUnion,
  arrayRemove
}
